import Footer from '../../components/footer/footer';
import Header from '../../components/header/header';


function Cart() {
  return (
    <div>
      <Header/>
      <main className="page-content">
        <div className="container">
          <h1 className="title title--bigger page-content__title"></h1>
          <ul className="breadcrumbs page-content__breadcrumbs page-content__breadcrumbs--on-cart-page">
            <li className="breadcrumbs__item"><a className="link" href="./main.html"></a>
            </li>
            <li className="breadcrumbs__item"><a className="link" href="./main.html"></a>
            </li>
            <li className="breadcrumbs__item"><a className="link"></a>
            </li>
          </ul>
          <div className="cart">
            <div className="cart-item">
              <button className="cart-item__close-button button-cross" type="button" aria-label=""><span className="button-cross__icon"></span><span className="cart-item__close-button-interactive-area"></span>
              </button>
              <div className="cart-item__image"><img src="img/content/catalog-product-2.jpg" srcSet="img/content/catalog-product-2@2x.jpg 2x" width="55" height="130" alt="  bass"/>
              </div>
              <div className="product-info cart-item__info">
                <p className="product-info__title">  bass</p>
                <p className="product-info__info">: SO757575</p>
                <p className="product-info__info">, 6 </p>
              </div>
              <div className="cart-item__price">17 500 </div>
              <div className="quantity cart-item__quantity">
                <button className="quantity__button" aria-label=" ">
                  <svg width="8" height="8" aria-hidden="true">
                    <use xlinkHref="#icon-minus"></use>
                  </svg>
                </button>
                <input className="quantity__input" type="number" placeholder="1" id="2-count" name="2-count" max="99"/>
                <button className="quantity__button" aria-label=" ">
                  <svg width="8" height="8" aria-hidden="true">
                    <use xlinkHref="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <div className="cart-item__price-total">17 500 </div>
            </div>
            <div className="cart-item">
              <button className="cart-item__close-button button-cross" type="button" aria-label=""><span className="button-cross__icon"></span><span className="cart-item__close-button-interactive-area"></span>
              </button>
              <div className="cart-item__image"><img src="img/content/catalog-product-4.jpg" srcSet="img/content/catalog-product-4@2x.jpg 2x" width="55" height="130" alt="URT Z30 Plus"/>
              </div>
              <div className="product-info cart-item__info">
                <p className="product-info__title">URT Z30 Plus</p>
                <p className="product-info__info">: SO754565</p>
                <p className="product-info__info">, 6 </p>
              </div>
              <div className="cart-item__price">34 500 </div>
              <div className="quantity cart-item__quantity">
                <button className="quantity__button" aria-label=" ">
                  <svg width="8" height="8" aria-hidden="true">
                    <use xlinkHref="#icon-minus"></use>
                  </svg>
                </button>
                <input className="quantity__input" type="number" placeholder="1" id="4-count" name="4-count" max="99"/>
                <button className="quantity__button" aria-label=" ">
                  <svg width="8" height="8" aria-hidden="true">
                    <use xlinkHref="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <div className="cart-item__price-total">34 500 </div>
            </div>
            <div className="cart__footer">
              <div className="cart__coupon coupon">
                <h2 className="title title--little coupon__title">  </h2>
                <p className="coupon__info">  ,     .</p>
                <form className="coupon__form" id="coupon-form" method="post" action="/">
                  <div className="form-input coupon__input">
                    <label className="visually-hidden"></label>
                    <input type="text" placeholder=" " id="coupon" name="coupon"/>
                    <p className="form-input__message form-input__message--success"> </p>
                  </div>
                  <button className="button button--big coupon__button"></button>
                </form>
              </div>
              <div className="cart__total-info">
                <p className="cart__total-item"><span className="cart__total-value-name">:</span><span className="cart__total-value">52 000 </span></p>
                <p className="cart__total-item"><span className="cart__total-value-name">:</span><span className="cart__total-value cart__total-value--bonus">- 3000 </span></p>
                <p className="cart__total-item"><span className="cart__total-value-name"> :</span><span className="cart__total-value cart__total-value--payment">49 000 </span></p>
                <button className="button button--red button--big cart__order-button"> </button>
              </div>
            </div>
          </div>
        </div>
      </main>
      <Footer/>
    </div>
  );
}

export default Cart;
