import { Link } from 'react-router-dom';
import spriteAuto from '../../assets/icon-basket.svg';

function Header() {
  return (
    <div id='headLink'>
      <header className="header" id="header">
        <div className="container header__wrapper"><a className="header__logo logo" href="main.html"><img className="logo__img" width="70" height="70" src="./img/svg/logo.svg" alt=""/></a>
          <nav className="main-nav">
            <ul className="main-nav__list">
              <li><Link className="link main-nav__link" to='/'></Link>
              </li>
              <li><Link className="link main-nav__link" to="#"> ?</Link>
              </li>
              <li><Link className="link main-nav__link" to="#"> </Link>
              </li>
            </ul>
          </nav>
          <div className="form-search">
            <form className="form-search__form" id="form-search">
              <button className="form-search__submit" type="submit">
                <svg className="form-search__icon" width="14" height="15" aria-hidden="true">
                  <use xlinkHref="#icon-search"></use>
                </svg><span className="visually-hidden"> </span>
              </button>
              <input className="form-search__input" id="search" type="text" autoComplete="off" placeholder="  ?" />
              <label className="visually-hidden" htmlFor="search"></label>
            </form>
            <ul className="form-search__select-list hidden">
              <li className="form-search__select-item" tabIndex={0}> Plus</li>
              <li className="form-search__select-item" tabIndex={0}> UX</li>
              <li className="form-search__select-item" tabIndex={0}> UX2</li>
              <li className="form-search__select-item" tabIndex={0}> UX3</li>
              <li className="form-search__select-item" tabIndex={0}> UX4</li>
              <li className="form-search__select-item" tabIndex={0}> UX5</li>
            </ul>
            <button className="form-search__reset" type="reset" form="form-search">
              <svg className="form-search__icon" width="14" height="15" aria-hidden="true">
                <use xlinkHref="#icon-close"></use>
              </svg><span className="visually-hidden"> </span>
            </button>
          </div>
          {/*     */}
          <a className="header__cart-link" href="#" aria-label="">
            <svg className="header__cart-icon" width="14" height="14" aria-hidden="true">
              <use xlinkHref="#icon-basket"></use>
              <img src={spriteAuto} alt='' className="header__cart-icon" width="14" height="14" aria-hidden="true" />
            </svg><span className="visually-hidden">  </span><span className="header__cart-count">2</span>
            <img src={spriteAuto} alt='' />
          </a>
          {/* ------------------------------------------------ */}
        </div>
      </header>
    </div>
  );
}

export default Header;
